<template>
	<div class="PirateList">
		<Row :gutter="15">

			<Col :span="6" class="product" v-for="(item, index) in list" :key="item._id + index">
        <div class="card">
          <Row>
            <Col :span="8">
              <img :src="item.pic" alt="" width="100%">
            </Col>
            <Col :span="16">
              <h3 class="product_name">{{ item.product_name }}</h3>
              <p class="desc">{{ item.readme }}</p>
              <p class="price">单价：¥{{ item.price }}</p>
            </Col>
          </Row>
          <div class="opera">

            <p class="btn-group" v-if="$store.state.power == 1">
              <Button type="primary" size="small">详情</Button>
              <Button type="warning" size="small">购买</Button>
            </p>

            <p class="btn-group" v-else-if="$store.state.power == 0">
              <Button type="success" size="small">编辑</Button>
              <i-switch v-model="item.switch">
                <span slot="open">开</span>
                <span slot="close">关</span>
              </i-switch>
            </p>

          </div>
        </div>
			</Col>

		</Row>
	</div>
</template>

<script>
export default {
  name: "PirateList",
  data() {
    return {
      list: [
        {
          _id: Math.random(),
          product_name: "拼图碎片",
          readme: "购买服务后将提供当前拼图最稀缺的碎片*1",
          price: 2,
          pic:
            "data:image/svg+xml;base64,",
          switch: true
        },
        {
          _id: Math.random(),
          product_name: "刀枪帽",
          readme: "购买服务后将提供当前拼图最稀缺的碎片*1",
          price: 2,
          pic:
            "data:image/svg+xml;base64,",
          switch: true
        },
        {
          _id: Math.random(),
          product_name: "拼图碎片",
          readme: "购买服务后将提供当前拼图最稀缺的碎片*1",
          price: 2,
          pic:
            "data:image/svg+xml;base64,",
          switch: true
        },
        {
          _id: Math.random(),
          product_name: "拼图碎片",
          readme: "购买服务后将提供当前拼图最稀缺的碎片*1",
          price: 2,
          pic:
            "data:image/svg+xml;base64,",
          switch: true
        },
        {
          _id: Math.random(),
          product_name: "拼图碎片",
          readme: "购买服务后将提供当前拼图最稀缺的碎片*1",
          price: 2,
          pic:
            "data:image/svg+xml;base64,",
          switch: true
        }
      ]
    };
  },
  methods: {
    
  }
};
</script>

<style lang="scss" scoped>
.product {
  &:hover {
    .card {
      box-shadow: 0px 0px 2px 1px #eee;
    }
  }
  .card {
    padding: 12px;
    border: 1px solid #e5e5e5;
    border-radius: 5px;
    margin-bottom: 15px;

    .price {
      color: tomato;
      margin-bottom: 6px;
    }
    .opera {
      border-top: 1px solid #f0f0f0;
      .btn-group {
        margin-top: 8px;
      }
    }
  }
}
</style>



